* {
    margin: 0;
    font-family: Inter;
    background: #FAFAFC;
}

.navbar {
    background-color: #1C2630;
}

.button {
    margin: 2% 2% 2% 10%;
    background-color: #1C2630;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 15px;
    cursor: pointer;
    transition: 0.3s;
}

.button:hover {
    color: #938D6C;
}

.button:active {
    color: #938D6C;
}

h1 {
    font-weight: 600;
    font-size: 24px;
    color: #1C2630;
    padding: 1%;
    margin: 20px 0 0 20px;
}

.product {
    display: inline-block;
    padding: 1%;
    margin-left: 20px;
}

.product img {
    width: 220px;
    height: auto;
    background: #EFEFF0;
    border-radius: 5px;
    transition: 0.3s;
}

.product img:hover {
    background-color: #e3e4e3;
  }

.product h2 {
    font-weight: 600;
    font-size: 15px;
    color: #1C2630;
}

.product p {
    font-weight: 400;
    font-size: 15px;
    color: #1C2630;
    margin-top: 2%;
}

.bt1 {
    background-color: #1C2630;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 15px;
    cursor: pointer;
    font-family: 'Inter';
    font-weight: 400;
    font-size: 11px;
    border-radius: 5px;
    width: 130px;
    height: 40px;
    margin-top: 2%;
}

.bt2 {
    background-color: #1c263000;
    border: solid 1px;
    color: #1C2630;
    text-align: center;
    text-decoration: none;
    font-size: 15px;
    cursor: pointer;
    font-family: 'Inter';
    font-weight: 400;
    font-size: 11px;
    border-radius: 5px;
    width: 80px;
    height: 40px;
    margin-left: 3%;
}

.cont {
    background-color: #E8EBEA;
    width: 100%;
    height: 224px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 90px;
}

.cont img {
    background-color: #e8ebea00;
}

.cont .img2 {
    height: auto;
    width: 270px;
}

.cont p {
    background-color: #e8ebea00;
    margin-top: 5%;
}

.sale {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 800;
    font-size: 80px;
    line-height: 103px;
    background-color: #e8ebea00;
    letter-spacing: 0.06em;
    color: #1C2630;
}

.off {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 300;
    font-size: 80px;
    line-height: 103px;
    background-color: #e8ebea00;
    letter-spacing: 0.06em;
    color: #1C2630;
}