.bag {
    margin-top: 110px;
}

.p {
    font-weight: 400;
    font-size: 18px;
    color: #1C2630;
    margin-left: 40px;
}

.img {
    width: 200px;
    height: auto;
    background: #EFEFF0;
    border-radius: 9px;
}

.productName {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    color: #1C2630;
}

.productPrice {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    color: #1C2630;
}

.li {
    list-style-type: none;
    margin-left: 3%;
    display: inline-block;
}

.button {
    background-color: #1C2630;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 15px;
    cursor: pointer;
    font-family: 'Inter';
    font-weight: 400;
    font-size: 12px;
    border-radius: 5px;
    width: 200PX;
    height: 40px;
    margin-top: 2%;
}

.confirmButton{
    background-color: #1C2630;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 15px;
    cursor: pointer;
    font-family: 'Inter';
    font-weight: 400;
    font-size: 14px;
    border-radius: 20px;
    width: 250PX;
    height: 50px;
    float: right;
    margin: 1% 3% 0 0;
}