* {
    margin: 0;
    font-family: Inter;
    background: #FAFAFC;
}

.navbar {
    background-color: #1C2630;
    display: flex;
    position: fixed;
    top: 0;
    width: 100%;
}

.button {
    margin-left: 10%;
    background-color: #1C2630;
    /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 17px;
    cursor: pointer;
}

.button:hover {
    color: #938D6C;
}

.button:active {
    color: #938D6C;
}

h1 {
    font-weight: 600;
    font-size: 24px;
    color: #1C2630;
    padding: 1%;
    margin: 20px 0 0 20px;
}

.product {
    display: inline-block;
    padding: 1%;
    margin-left: 20px;
}

.product img {
    width: 220px;
    height: auto;
    background: #EFEFF0;
    border-radius: 5px;
}

.product h2 {
    font-weight: 500;
    font-size: 15px;
    color: #1C2630;
}

.product p {
    font-weight: 400;
    font-size: 15px;
    color: #1C2630;
    margin-top: 2%;
}

.bt1 {
    background-color: #1C2630;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 15px;
    cursor: pointer;
    font-family: 'Inter';
    font-weight: 300;
    font-size: 11px;
    border-radius: 5px;
    width: 130px;
    height: 40px;
    margin-top: 2%;
}

.bt2 {
    background-color: #1c263000;
    border: solid 1px;
    color: #1C2630;
    text-align: center;
    text-decoration: none;
    font-size: 15px;
    cursor: pointer;
    font-family: 'Inter';
    font-weight: 300;
    font-size: 11px;
    border-radius: 5px;
    width: 80px;
    height: 40px;
    margin-left: 3%;
}

.icon {
    background-color: #1c263000;
    height: auto;
    width: 50px;
    margin: 1.5% 0 1.5% 1.5%;
}